@-webkit-keyframes first-animation {
0% { -webkit-transform: scale(1,1); transform: scale(1,1);}
50% { -webkit-transform: scale(0.8,0.8); transform: scale(0.8,0.8); }
100% { -webkit-transform: scale(1,1); transform: scale(1,1); }
}

@keyframes first-animation {
0% { -webkit-transform: scale(1,1); transform: scale(1,1);}
50% { -webkit-transform: scale(0.8,0.8); transform: scale(0.8,0.8); }
100% { -webkit-transform: scale(1,1); transform: scale(1,1); }
}
@-webkit-keyframes second-animation {
0% { -webkit-transform: scale(1,1); transform: scale(1,1); }
70% { -webkit-transform: scale(0.7,0.7); transform: scale(0.7,0.7); }
80% { -webkit-transform: scale(0.9,0.9); transform: scale(0.9,0.9); }
100% { -webkit-transform: scale(1,1); transform: scale(1,1);  }
}
@keyframes second-animation {
0% { -webkit-transform: scale(1,1); transform: scale(1,1); }
70% { -webkit-transform: scale(0.7,0.7); transform: scale(0.7,0.7); }
80% { -webkit-transform: scale(0.9,0.9); transform: scale(0.9,0.9); }
100% { -webkit-transform: scale(1,1); transform: scale(1,1);  }
}


